body {
    font-family: 'Roboto';
    font-weight: 400;
    font-style: normal;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
h1,h2,h3,h4,h5,p,span,ul,li,button {
    margin: 0;
    padding: 0;
    text-align: center;
}
ul {
    list-style: none;
    display: flex;
}
.container {
    margin: 0 auto;
    max-width: 1024px;
}